import React from "react";
import { RouteData } from "react-static";
import { Helmet } from "react-helmet";
import Page from "../containers/Page";

export default () => (
  <RouteData>
    {({ posts, post }) => (
      <React.Fragment>
        <Helmet>
          <meta name="Description" content={post.data.description} />
          <meta
            property="og:url"
            content={`https://www.evular.lt/blogas/${post.data.slug}`}
          />
          <meta property="og:title" content={post.data.title} />
          <meta property="og:image" content={post.data.thumbnail} />
          <meta
            property="og:description"
            content="EVULAR yra informacini technologij sprendimus teikianti bendrov. Mes specializuojames internetini puslapi krimo, informacini sistem prieiros, duomen analitikos ir kt. paslaugose. Didel dmes skiriame naujausi interneto technologij taikymui ir kibernetiniam saugumui."
          />
          <title>{post.data.title}</title>
        </Helmet>
        <Page items={posts} item={post} page="blogas" />
      </React.Fragment>
    )}
  </RouteData>
);
